import React from "react";


export default class WinMenu extends React.Component {

    props: {
        winner: "white" | "black" | "";
        restart: Function,
    }

    constructor(props: any) {
        super(props);
        this.props = props;
    }
    handleClick = () => {
        this.props.restart();
    }
    render() {
        let winner: string = this.props.winner
        if (winner) {
            winner = winner[0].toUpperCase() + winner.slice(1);
        }
        return (
            <div id="winMenu">
                <h1>{winner} Wins!</h1>
                <h2 onClick={this.handleClick}>Play Again?</h2>
            </div>
        )
    }
}